export default {
  name: "user.dialog",
  props: {
    show: undefined,
    text: undefined
  },
  created() {
    if (!this.text) {
      this.text = this.$l('user.login_tip');
    }
    console.log('text:',this.text);
    console.log("_user_dialog:",this);
    window._user_dialog = this;
  },
  methods: {
    on_register: function () {
      this.show = false;
      this.go('/user/register');
    },
    on_login: function () {
      this.show = false;
      this.go('/user/login');
    },
  },
  watch: {
    show: function (newValue, oldValue) {
      console.log('call watch show change1');
      this.$emit('update:show', newValue);
    }
  },
  data() {
    return {
    }
  },
}
